
@charset "UTF-8";
.operation-container {
  /*   */
}
.operation-container .h_title {
    width: 100%;
    height: 150px;
    line-height: 120px;
    text-align: center;
    font-size: 50px;
    font-weight: 200;
    color: #CDCED3;
    position: relative;
}
.operation-container .h_title .username {
      line-height: initial;
      font-size: 16px;
      font-weight: normal;
      position: absolute;
      left: 50%;
      bottom: 20%;
      -webkit-transform: translate(-50%);
              transform: translate(-50%);
}
.operation-container .h_main {
    width: 90%;
    max-width: 640px;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
            flex-direction: column;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.operation-container .h_main .each_part {
      width: 100%;
      height: 44px;
      line-height: 44px;
      padding: 0 8px;
      text-align: center;
      box-sizing: border-box;
      color: #fff;
      font-weight: 600;
      background: #5077AA;
      margin-top: 20px;
      border-radius: 4px;
      position: relative;
}
.operation-container .h_main .each_part .right_arrow {
        position: absolute;
        right: 8px;
        top: 50%;
        -webkit-transform: translate(0, -50%);
                transform: translate(0, -50%);
        fill: #fff;
}
.operation-container .tech_bottom {
    width: 100%;
    max-width: 640px;
    text-align: center;
    position: fixed;
    bottom: 0;
    font-size: 16px;
    color: #CDCED3;
    font-weight: 200;
}
.operation-container .cp_name {
    color: dodgerblue;
}



