
@charset "UTF-8";
.flow-detail-container {
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  /*  */
}
.flow-detail-container .flows {
    display: inline-block;
    margin: 0 auto 0 0;
    padding: 10px 20px;
    height: 300px;
}
.flow-detail-container .f_title {
    width: 100%;
    height: 80px;
    line-height: 80px;
    font-size: 34px;
    text-align: center;
    color: #fff;
    font-weight: 200;
    background: #5077aa;
    position: relative;
}
.flow-detail-container .f_title .s_user {
      line-height: initial;
      font-size: 16px;
      font-weight: normal;
      position: absolute;
      left: 50%;
      bottom: 20%;
      -webkit-transform: translate(-50%);
              transform: translate(-50%);
      display: -webkit-box;
      display: -webkit-flex;
      display: flex;
      -webkit-box-align: center;
      -webkit-align-items: center;
              align-items: center;
}
.flow-detail-container .f_title .s_user .right_arrow {
        fill: #fff;
}
.flow-detail-container .f_main {
    width: 90%;
    max-width: 600px;
    position: absolute;
    top: 70px;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    z-index: 100;
    padding-bottom: 56px;
}
.flow-detail-container .f_main .f_main_wrapper {
      height: 100%;
      border-radius: 4px;
      background: #fff;
      box-shadow: 0 2px 10px #e8e8e8;
      overflow: hidden;
}
.flow-detail-container .f_button {
    width: 100%;
    height: 44px;
    line-height: 44px;
    text-align: center;
    background: #5077aa;
    color: #fff;
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: 100;
}
.flow-detail-container .comment-container {
    padding: 0 20px;
}
.flow-detail-container .comment-container .comment-item {
      display: -webkit-box;
      display: -webkit-flex;
      display: flex;
      -webkit-box-pack: justify;
      -webkit-justify-content: space-between;
              justify-content: space-between;
      margin: 10px 0;
}
.flow-detail-container .comment-container .comment-item .comment-left {
        -webkit-box-flex: 1;
        -webkit-flex: 1;
                flex: 1;
}
.flow-detail-container .comment-container .comment-item .comment-left .title {
          margin-bottom: 5px;
}
.flow-detail-container .comment-container .comment-item .comment-left .time {
          color: #cecece;
          font-size: 14px;
}
.flow-detail-container .comment-container .comment-item .comment-right {
        -webkit-box-flex: 1;
        -webkit-flex: 1;
                flex: 1;
        padding-left: 10px;
        text-align: right;
        font-size: 14px;
}



