
.search-list-container {
  position: absolute;
  right: 0;
  z-index: 100;
  width: 50%;
  max-height: 300px;
  color: #000;
  box-shadow: 0 2px 10px #e8e8e8;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.search-list-container .weui-cells {
    margin-top: 0;
}
.search-list-container .weui-cell {
    background-color: #fff;
    text-align: right;
}
.f_title {
  width: 100%;
  height: 80px;
  line-height: 80px;
  font-size: 34px;
  text-align: center;
  color: #fff;
  font-weight: 200;
  background: #5077aa;
  position: relative;
}
.f_title .s_user {
    line-height: initial;
    font-size: 16px;
    font-weight: normal;
    position: absolute;
    left: 50%;
    bottom: 20%;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
            align-items: center;
}
.f_title .s_user .right_arrow {
      fill: #fff;
}
.f_main {
  width: 90%;
  max-width: 600px;
  position: absolute;
  top: 70px;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  z-index: 100;
  padding-bottom: 56px;
}
.f_main .f_main_wrapper {
    height: 100%;
    border-radius: 4px;
    background: #fff;
    box-shadow: 0 2px 10px #e8e8e8;
    overflow: hidden;
}
.f_button {
  width: 100%;
  height: 44px;
  line-height: 44px;
  text-align: center;
  background: #5077aa;
  color: #fff;
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 100;
}



